  color: rgb(167, 164, 164);
  background: -webkit-linear-gradient(0deg,#daaada, #97D9E1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
}

.navbar-item:hover {
  cursor: pointer;
}

