.client-dashboard-container {
  position: relative;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(13, 1fr);
  column-gap: 2em;
  height: 95vh;
  width: 78vw;
  padding: 1.3em;
  /* background-color: red; */
  background-image: url('./dashboardlayer.svg');
  background-repeat: no-repeat;
  background-position: center;
  aspect-ratio: 3/2; 
  background-size: cover; 
}

.client-engagements-widget {
  position: relative;
  transform-style: preserve-3d;
  /* background-image: linear-gradient(to right, #3ab5b0 0%, #3d99be 31%, #56317a 100%); */
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
  border-radius: 0.5em;
  grid-column: 1 / 3;
  grid-row: 1 / 5;
}

.current-plan-widget {
  position: relative;
  transform-style: preserve-3d;
  background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);

  /* background-image: linear-gradient(to right, #3ab5b0 0%, #3d99be 31%, #56317a 100%); */
  border-radius: 0.5em;
  grid-column: 3 / 4;
  grid-row: 1 / 3;
}

.current-case-widget {
  position: relative;
  transform-style: preserve-3d;
  /* background-image: linear-gradient(to right, #3ab5b0 0%, #3d99be 31%, #56317a 100%); */
  background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
  border-radius: 0.5em;
  grid-column: 4 / 7;
  grid-row: 1 / 5;
}

.current-case-status-widget {
  position: relative;
  transform-style: preserve-3d;
  /* background-image: linear-gradient(to right, #3ab5b0 0%, #3d99be 31%, #56317a 100%); */
  background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
  border-radius: 0.5em;
  grid-column: 1 / 4;
  grid-row: 6 / 9;
}

.payment-info-widget {
  position: relative;
  transform-style: preserve-3d;
  /* background-image: linear-gradient(to right, #3ab5b0 0%, #3d99be 31%, #56317a 100%); */
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
  border-radius: 0.5em;
  grid-column: 4 / 7;
  grid-row: 6 / 10;
}

.case-history-widget {
  position: relative;
  transform-style: preserve-3d;
  /* background-image: linear-gradient(to right, #3ab5b0 0%, #3d99be 31%, #56317a 100%); */
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
  border-radius: 0.5em;
  grid-column: 1 / 4;
  grid-row: 10 / 14;
}

.client-unread-messages-widget {
  position: relative;
  transform-style: preserve-3d;
  /* background-image: linear-gradient(to right, #3ab5b0 0%, #3d99be 31%, #56317a 100%); */
