  border-radius: 0.5em;
  grid-column: 5 / 7;
  grid-row: 11 / 14;
}

.client-engagements-widget:hover, .current-plan-widget:hover, .current-case-widget:hover, .current-case-status-widget:hover, .payment-info-widget:hover, .case-history-widget:hover, .client-unread-messages-widget:hover{
